import { useState } from "react";
import { useMeeting } from "@videosdk.live/react-sdk";
import { confirmAlert } from "react-confirm-alert"; // Import
import ParticipantsView from "../Participants/ParticipantsView/ParticipantsView";
import onParticipantJoined from "../../../utils/handlers/onParticipantJoined";
import onParticipantLeft from "../../../utils/handlers/onParticipantLeft";
import onSpeakerChanged from "../../../utils/handlers/onSpeakerChanged";
import Header from "../Header/Header";

export default function MeetingView({ onNewMeetingIdToken, onMeetingLeave }) {
  const [participantViewVisible, setParticipantViewVisible] = useState(true);
  const [streamKey, setStreamKey] = useState("");
  const [streamKeyVisibility, setStreamKeyVisibility] = useState(false);

  function handleStreamKeyChange(event) {
    this.setStreamKey(event.target.value);
  }

  function onClickStreamKeyChangeButton() {
    setStreamKeyVisibility(!streamKeyVisibility);
  }
  function onMeetingJoined() {
    console.log("onMeetingJoined");
  }
  function onMeetingLeft() {
    console.log("onMeetingLeft");
    onMeetingLeave();
  }
  const onLiveStreamStarted = (data) => {
    console.log("onLiveStreamStarted example", data);
  };
  const onLiveStreamStopped = (data) => {
    console.log("onLiveStreamStopped example", data);
  };

  const {
    meetingId,
    meeting,
    localParticipant,
    mainParticipant,
    activeSpeakerId,
    participants,
    presenterId,
    localMicOn,
    localWebcamOn,
    localScreenShareOn,
    messages,
    isRecording,
    isLiveStreaming,
    pinnedParticipants,
    //
    join,
    leave,
    connectTo,
    end,
    //
    startRecording,
    stopRecording,
    //
    respondEntry,
    //
    muteMic,
    unmuteMic,
    toggleMic,
    //
    disableWebcam,
    enableWebcam,
    toggleWebcam,
    //
    disableScreenShare,
    enableScreenShare,
    toggleScreenShare,
    //
    getMics,
    getWebcams,
    changeWebcam,
    changeMic,

    startLivestream,
    stopLivestream,
  } = useMeeting({
    onParticipantJoined,
    onParticipantLeft,
    onSpeakerChanged,
    onMeetingJoined,
    onMeetingLeft,
    onLiveStreamStarted,
    onLiveStreamStopped,
  });

  const handleStartLiveStream = () => {
    const streamKey = document.getElementById("streamKey").value;

    startLivestream([
      {
        url: "rtmp://a.rtmp.youtube.com/live2",
        streamKey: streamKey,
      },
    ]);
  };
  const handleStopLiveStream = () => {
    stopLivestream();
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        backgroundColor: "#1B2432",
        color: "white",
      }}
    >
      <Header
        leave={leave}
        toggleMic={toggleMic}
        toggleWebcam={toggleWebcam}
        streamKeyVisibility={streamKeyVisibility}
        onClickStreamKeyChangeButton={onClickStreamKeyChangeButton}
        handleStartLiveStream={handleStartLiveStream}
        handleStopLiveStream={handleStopLiveStream}
      />
      <h4 style={{ textAlign: "center" }}>Meeting id is : {meetingId}</h4>
      <div style={{ display: "flex", flex: 1 }}>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            position: "relative",
            flex: 1,
            height: "90vh",
          }}
        >
          <ParticipantsView />
        </div>
      </div>
    </div>
  );
}
