import { Button, ButtonGroup, ThemeProvider, TextField } from "@mui/material";
import { Link } from "react-router-dom";
import generateMuiTheme from "../../../mui/theme";

export default function Header(props) {
  return (
    <ThemeProvider theme={generateMuiTheme()}>
      <div
        style={{
          backgroundColor: "#121420",
          display: "flex",
        }}
      >
        <Link to="/" onClick={props.leave}>
          <Button>LEAVE</Button>
        </Link>
        <ButtonGroup
          variant="text"
          style={{ alignSelf: "center", margin: "auto" }}
        >
          <Button onClick={props.toggleMic}>toggleMic</Button>
          <Button
            onClick={() => {
              props.toggleWebcam();
            }}
          >
            toggleWebcam
          </Button>

          <Button onClick={props.handleStartLiveStream}>
            Start Live Stream
          </Button>
          <Button onClick={props.handleStopLiveStream}>Stop Live Stream</Button>
        </ButtonGroup>
        <div style={{ display: "inline", marginRight: 0, marginLeft: "auto" }}>
          <TextField
            id="streamKey"
            label="Stream Key"
            variant="outlined"
            type={props.streamKeyVisibility ? "text" : "password"}
            size="small"
          />
          <Button onClick={props.onClickStreamKeyChangeButton}>
            Show StreamKey
          </Button>
        </div>
      </div>
    </ThemeProvider>
  );
}
