import React, { useState } from "react";
import { MeetingProvider } from "@videosdk.live/react-sdk";
import "react-confirm-alert/src/react-confirm-alert.css"; // Import css
import {
  BrowserRouter,
  Routes,
  Route,
  Link,
  useParams,
  HashRouter,
} from "react-router-dom";
import { JoiningScreen } from "./components/PreMeetingScreen/JoiningScreen/JoiningScreen";
import MeetingView from "./components/MeetingScreen/MeetingView/MeetingView";

function Element({
  micOn,
  webcamOn,
  participantName,
  setMeetingId,
  setToken,
  setWebcamOn,
  setMicOn,
}) {
  const { meetingId, token } = useParams();

  return (
    <MeetingProvider
      config={{
        meetingId: meetingId,
        micEnabled: micOn,
        webcamEnabled: webcamOn,
        name: participantName ? participantName : "TestUser",
      }}
      token={token}
      reinitialiseMeetingOnConfigChange={true}
      joinWithoutUserInteraction={true}
    >
      <MeetingView
        onNewMeetingIdToken={({ meetingId, token }) => {
          setMeetingId(meetingId);
          setToken(token);
        }}
        onMeetingLeave={() => {
          setToken("");
          setMeetingId("");
          setWebcamOn(false);
          setMicOn(false);
        }}
      />
    </MeetingProvider>
  );
}

const App = () => {
  const [token, setToken] = useState("");
  const [meetingId, setMeetingId] = useState("");
  const [participantName, setParticipantName] = useState("");
  const [micOn, setMicOn] = useState(false);
  const [webcamOn, setWebcamOn] = useState(false);

  return (
    <HashRouter>
      <Routes>
        <Route
          exact
          path="/"
          element={
            <JoiningScreen
              participantName={participantName}
              setParticipantName={setParticipantName}
              meetingId={meetingId}
              setMeetingId={setMeetingId}
              token={token}
              setToken={setToken}
              setMicOn={setMicOn}
              micOn={micOn}
              webcamOn={webcamOn}
              setWebcamOn={setWebcamOn}
            />
          }
        />
        <Route
          path="/meeting/:meetingId/:token"
          element={
            <Element
              micOn={micOn}
              webcamOn={webcamOn}
              participantName={participantName}
              setMeetingId={setMeetingId}
              setToken={setToken}
              setWebcamOn={setWebcamOn}
              setMicOn={setMicOn}
            />
          }
        />
      </Routes>
    </HashRouter>
  );
};

export default App;
